*,
*::before,
*::after {
  box-sizing: border-box; }

a {
  text-decoration: none;
  color: inherit;
  cursor: pointer; }

button {
  background-color: transparent;
  color: inherit;
  border-width: 0;
  padding: 0;
  cursor: pointer; }

figure {
  margin: 0; }

input::-moz-focus-inner {
  border: 0;
  padding: 0;
  margin: 0; }

ul,
ol,
dd {
  margin: 0;
  padding: 0;
  list-style: none; }

h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
  font-size: inherit;
  font-weight: inherit; }

p {
  margin: 0; }

cite {
  font-style: normal; }

fieldset {
  border-width: 0;
  padding: 0;
  margin: 0; }

.c1, .c2, .c3, .c4, .c5, .c6, .c7, .c8, .c9, .c10 {
  display: flex;
  flex-flow: column;
  justify-content: center;
  align-items: center;
  overflow: hidden;
  max-width: 100%; }
  .c1 > *, .c2 > *, .c3 > *, .c4 > *, .c5 > *, .c6 > *, .c7 > *, .c8 > *, .c9 > *, .c10 > * {
    flex-basis: 100%;
    flex: 1 1 auto; }

.c2 > * {
  flex-basis: 50%; }

.c3 > * {
  flex-basis: 100% / 3; }

.c4 > * {
  flex-basis: 25%; }

.c5 > * {
  flex-basis: 20%; }

.c6 > * {
  flex-basis: 100% / 6; }
